document.addEventListener("DOMContentLoaded", () => {
  const select = () => {
    const selects = document.querySelectorAll('.js-select');
  
    selects.forEach((select) => {
      const btn = select.querySelector('.js-select-btn');
      const text = select.querySelector('.js-select-text');
      const options = select.querySelectorAll('.js-select-option');
  
      const close = () => {
        select.classList.remove('active');
      };
  
      const open = () => {
        select.classList.add('active');
      };
  
      btn.addEventListener('click', (e) => {
        const isActive = select.classList.contains('active');
  
        if(isActive){
          close();
        }else {
          open();
        }
      });
  
      options.forEach((option) => {
        option.addEventListener('click', () => {
          const value = option.innerText;
          text.innerText = value;
  
          close();
        });
      });
  
  
      document.documentElement.addEventListener('click', function (event) {
      	var isClickWithinOpenedDiv = top_walker(event.target, function (node) {
      		return node === select
      	});
      	if (isClickWithinOpenedDiv) {
  
      	}else{
          close();
        }
      }, true);
    });
  
  
  };
  
  const field = () => {
    const fields = document.querySelectorAll('.js-field');
  
    fields.forEach((field) => {
      const input = field.querySelector('.js-field-input');
      const isValue = input.value.length != 0;
  
      const filled = () => {
        field.classList.add('filled');
      };
  
      const empty = () => {
        field.classList.remove('filled');
      };
  
      if(isValue){
        filled();
      }
  
      input.addEventListener('keyup', () => {
        const isValue = input.value.length != 0;
  
        if(isValue){
          filled();
        }else{
          empty();
        }
      });
    });
  };
  
  const headerMenu = () => {
  	const menu = document.querySelector('.js-menu');
  	const btnOpen = document.querySelector('.js-menu-open');
  	const btnClose = menu.querySelector('.js-menu-close');
  	let isActive = false;
  
  	const open = () => {
  		menu.classList.add('active');
  		BG.body.classList.add('overflow', 'bg-black');
  		BG.siteContent.classList.add('hide');
  		BG.footer.classList.add('hide');
  	};
  
  	const close = () => {
  		menu.classList.remove('active');
  		BG.body.classList.remove('overflow', 'bg-black');
  		BG.siteContent.classList.remove('hide');
  		BG.footer.classList.remove('hide');
  	};
  
  	btnOpen.addEventListener('click', () => {
  		open();
  
  		isActive = true;
  	});
  
  	btnClose.addEventListener('click', () => {
  		if(isActive){
  			close();
  
  			isActive = false;
  		}
  	});
  };
  
  const categoryCarousel = () => {
    const sliders = document.querySelectorAll('.js-category-carousel');
  
    sliders.forEach((slider) => {
      let sliderObj = tns;
      const options = {
        container: '.js-category-carousel',
        autoWidth: true,
        items: 3.3,
        gutter: 6,
        mouseDrag: true,
        swipeAngle: false,
        controls: true,
        loop: false,
        nav: false,
        responsive: {
          0: {
            controls: false
          },
          1280: {
            controls: true
          }
        }
      };
  
      // slider init
      sliderObj = tns(options);
    });
  };
  
  const cardCart = () => {
    const btns = document.querySelectorAll('.js-card-cart-btn');
  
    btns.forEach((btn) => {
      btn.addEventListener('click', () => {
        const cardBottom = btn.closest('.js-card-bottom');
        const isActive = cardBottom.classList.contains('active');
  
        cardBottom.classList.add('active');
      });
    });
  };
  
  const counter = () => {
    const counters = document.querySelectorAll('.js-counter');
  
  
    function parentBy(el, filter) {
      if(typeof filter !== 'function')
        throw new Error('filter must be a function!');
      var parent = el.parentNode, last;
      while(parent) {
        last = parent;
        parent = parent.parentNode;
      }
      return last;
    }
  
    function findAncestor (el, cls) {
        while ((el = el.parentElement) && !el.classList.contains(cls));
      return el;
    }
  
  
    counters.forEach((counter) => {
      const btnMinus = counter.querySelector('.js-counter-btn-prev');
      const btnPlus = counter.querySelector('.js-counter-btn-next');
      const number = counter.querySelector('.js-counter-number');
      let currentValue = number.value;
  
      number.addEventListener('keyup', () => {
        const value = number.value;
  
        currentValue = value;
      });
  
      btnMinus.addEventListener('click', (e) => {
        currentValue--;
  
        var cardBottom = findAncestor(e.target, 'js-card-bottom');
  
        if(currentValue <= 0){
          currentValue = 1;
          cardBottom.classList.remove('active');
        }
  
        number.value = currentValue;
      });
  
      btnPlus.addEventListener('click', () => {
        currentValue++;
  
        number.value = currentValue;
      });
    });
  };
  
  const cartPopup = () => {
    const carts = document.querySelectorAll('.js-cart');
    const popup = document.querySelector('.js-cart-popup');
  
    carts.forEach((cart) => {
      cart.addEventListener('mouseenter', () => {
        popup.classList.add('active');
      });
  
      cart.addEventListener('mouseleave', () => {
        popup.classList.remove('active');
      });
      
      popup.addEventListener('mouseleave', () => {
        popup.classList.remove('active');
      });
  
    });
  };
  
  const catalogSlider = () => {
    const sliders = document.querySelectorAll('.js-catalog-slider');
  
    sliders.forEach((slider) => {
      let sliderObj = tns;
      const options = {
        container: '.js-catalog-slider',
        loop: false,
        items: 1,
        slideBy: 1,
        nav: true,
        controls: false,
        autoplay: false,
        speed: 400,
        autoplayButtonOutput: false,
        mouseDrag: true,
        lazyload: false,
        autowidth: true,
        center: false,
        swipeAngle: false,
        gutter: 10,
        autoHeight: true,
        responsive: {
          0: {
            disable: false,
            nav: true
          },
          1280: {
            disable: false,
            nav: true
          }
        }
      };
  
      sliderObj = tns(options);
    });
  };
  
  const category = () => {
    const categories = document.querySelectorAll('.js-category');
    const CATEGORY = {};
    CATEGORY.itemQuantity = 0;
    CATEGORY.itemQuantityDesktop = 8;
    CATEGORY.itemQuantitySmallDesktop = 6;
    CATEGORY.itemQuantityHorizontalTablet = 4;
    CATEGORY.itemQuantityVerticalTablet = 2;
    CATEGORY.changeQuantity = () => {
      const isDesktop = window.matchMedia("(min-width: 1280px)").matches;
      const isSmallDesktop = window.matchMedia("(min-width:1024px) and (max-width: 1279px)").matches;
      const isHorizontalTablet = window.matchMedia("(min-width:768px) and (max-width: 1023px)").matches;
      const isVerticalTablet = window.matchMedia("(max-width: 767px)").matches;
  
      const quantityDesktop = CATEGORY.itemQuantityDesktop
      const quantitySmallDesktop = CATEGORY.itemQuantitySmallDesktop;
      const quantityHorizontalTablet = CATEGORY.itemQuantityHorizontalTablet;
  
      const quantityVerticalTablet = CATEGORY.itemQuantityVerticalTablet;
  
  
      if(isDesktop){
        CATEGORY.itemQuantity = CATEGORY.itemQuantity + quantityDesktop;
      }
  
      if(isSmallDesktop){
        CATEGORY.itemQuantity = CATEGORY.itemQuantity + quantitySmallDesktop;
      }
  
      if(isHorizontalTablet){
        CATEGORY.itemQuantity = CATEGORY.itemQuantity + quantityHorizontalTablet;
      }
  
      if(isVerticalTablet){
        CATEGORY.itemQuantity = CATEGORY.itemQuantity + quantityVerticalTablet;
      }
  
      console.log(CATEGORY.itemQuantity);
    };
  
    categories.forEach((category) => {
      const items = category.querySelectorAll('.js-category-item');
      const btns = category.querySelectorAll('.js-category-btn');
  
      const changeItemCount = () => {
        const isDesktop = window.matchMedia("(min-width: 1280px)").matches;
        const isSmallDesktop = window.matchMedia("(min-width:1024px) and (max-width: 1279px)").matches;
        const isHorizontalTablet = window.matchMedia("(min-width:768px) and (max-width: 1023px)").matches;
        const isVerticalTablet = window.matchMedia("(max-width: 767px)").matches;
  
        if(isDesktop){
          CATEGORY.itemQuantity = CATEGORY.itemQuantityDesktop;
        }
  
        if(isSmallDesktop){
          CATEGORY.itemQuantity = CATEGORY.itemQuantitySmallDesktop;
        }
  
        if(isHorizontalTablet){
          CATEGORY.itemQuantity = CATEGORY.itemQuantityHorizontalTablet;
        }
  
        if(isVerticalTablet){
          CATEGORY.itemQuantity = CATEGORY.itemQuantityVerticalTablet;
        }
  
        items.forEach((item, index) => {
          const isItemActive = index < CATEGORY.itemQuantity;
  
          if(isItemActive){
            item.classList.remove('hide');
          }else{
            item.classList.add('hide');
          }
        });
      };
  
      changeItemCount();
  
      window.addEventListener('resize', () => {
        changeItemCount();
      });
  
      btns.forEach((btn) => {
        btn.addEventListener('click', () => {
          CATEGORY.changeQuantity();
  
          items.forEach((item, index) => {
            const isItemActive = index < CATEGORY.itemQuantity;
  
            if(isItemActive){
              item.classList.remove('hide');
            }else{
              item.classList.add('hide');
            }
          });
        });
      });
    });
  
  
  };
  
  const categorySelect = () => {
    const sections = document.querySelectorAll('.js-category-select');
  
    // global close
    sections.forEach((section) => {
      document.documentElement.addEventListener('click', function (event) {
      	var isClickWithinOpenedDiv = top_walker(event.target, function (node) {
      		return node === section
      	});
      	if (isClickWithinOpenedDiv) {
  
      	}else{
          const lists = section.querySelectorAll('.js-category-select-list');
  
          section.classList.remove('active');
          lists.forEach((list) => {
            list.classList.remove('active');
          });
        }
      }, true);
  
      const menuBtn = section.querySelector('.js-category-select-btn');
      const subBtns = section.querySelectorAll('.js-category-select-sub-btn');
      const links = section.querySelectorAll('.js-category-select-link');
      const placeholder = section.querySelector('.js-category-select-placeholder');
  
      // close function
      const close = () => {
        const lists = section.querySelectorAll('.js-category-select-list');
  
        section.classList.remove('active');
        lists.forEach((list) => {
          list.classList.remove('active');
        });
      };
  
      // menu toggle
      menuBtn.addEventListener('click', () => {
        const selectList = section.querySelector('.js-category-select-list');
        const isActive = selectList.classList.contains('active');
        const nestLists = section.querySelectorAll('.js-category-select-list');
        const nestBtns = section.querySelectorAll('.js-category-select-sub-btn');
  
        nestLists.forEach((nestList) => {
          nestList.classList.remove('active');
        });
  
        nestBtns.forEach((nestBtn) => {
          nestBtn.classList.remove('active');
        });
  
        if(isActive){
          selectList.classList.remove('active');
          section.classList.remove('active');
        }else{
          selectList.classList.add('active');
          section.classList.add('active');
        }
      });
  
      // menu nest listeners
      subBtns.forEach((subBtn) => {
        subBtn.addEventListener('mouseover', () => {
          const parentList = subBtn.closest('.js-category-select-list');
          const nestLists = parentList.querySelectorAll('.js-category-select-list');
          const nestBtns = parentList.querySelectorAll('.js-category-select-sub-btn');
          const currentItem = subBtn.closest('.js-category-select-item');
          const currentList = currentItem.querySelector('.js-category-select-list');
  
          // hide all nest lists & disable btns
          nestLists.forEach((nestList) => {
            nestList.classList.remove('active');
          });
  
          nestBtns.forEach((nestBtn) => {
            nestBtn.classList.remove('active');
          });
  
          // show current list
          currentList.classList.add('active');
          subBtn.classList.add('active');
        });
  
        subBtn.addEventListener('click', () => {
          const text = subBtn.innerText;
          placeholder.innerText = text;
  
          close();
        });
      });
  
  
      links.forEach((link) => {
        link.addEventListener('click', () => {
          const text = link.innerText;
          placeholder.innerText = text;
  
          close();
        });
      });
    });
  };
  
  const rating = () => {
    const ratings = document.querySelectorAll('.js-rating');
  
    ratings.forEach((rating) => {
      const items = rating.querySelectorAll('.js-rating-item');
  
      items.forEach((item, index) => {
        item.addEventListener('click', () => {
          const currentIndex = index;
  
          items.forEach((item, index) => {
            const isMore = currentIndex < index;
            const icon = item.querySelector('.js-rating-icon');
  
            const activeState = () => {
              item.classList.add('active');
              icon.setAttribute('src', 'assets/svg_icons/star-filled.svg');
            };
  
            const passiveState = () => {
              item.classList.remove('active');
              icon.setAttribute('src', 'assets/svg_icons/star-empty.svg');
            };
  
            if(isMore){
              passiveState();
            }else{
              activeState();
            }
          });
        });
      });
    });
  };
  
  const productCategory = () => {
    const sections = document.querySelectorAll('.js-product-category');
  
    var getSiblings = function (elem) {
  
    	// Setup siblings array and get the first sibling
    	var siblings = [];
    	var sibling = elem.parentNode.firstChild;
  
    	// Loop through each sibling and push to the array
    	while (sibling) {
    		if (sibling.nodeType === 1 && sibling !== elem) {
    			siblings.push(sibling);
    		}
    		sibling = sibling.nextSibling
    	}
  
    	return siblings;
  
    };
  
    sections.forEach((section) => {
      const btnRoot = section.querySelector('.js-product-category-root-btn');
      const btnRootTextNode = btnRoot.querySelector('.js-product-category-root-btn-text');
      const btnRootText = btnRootTextNode.innerHTML;
      const btns = section.querySelectorAll('.js-product-category-btn');
      const items = section.querySelectorAll('.js-product-category-item');
  
      btnRoot.addEventListener('click', () => {
        const isActive = section.classList.contains('active');
        const listChild = section.querySelector('.js-product-category-list');
        const items = section.querySelectorAll('.js-product-category-li');
  
        const expand = () => {
          listChild.classList.add('active');
          section.classList.add('active');
          items.forEach((item) => {
            item.classList.remove('hide');
          });
        };
  
        const stepBack = () => {
          const lists = section.querySelectorAll('.js-product-category-list');
          const activeLists = section.querySelectorAll('.js-product-category-list.active');
          const activeListLength = activeLists.length - 1;
          const listLength = lists.length - 1;
  
          activeLists.forEach((list, index) => {
            const lastActive = activeListLength == index;
            const rootActive = activeListLength == 0;
  
            if(rootActive){
              section.classList.remove('active');
  
              lists.forEach((list) => {
                list.classList.remove('active');
              });
  
              btns.forEach((btn) => {
                btn.classList.remove('hide');
              });
  
              items.forEach((item) => {
                item.classList.remove('hide');
              });
            }else{
              if(lastActive){
                const parentItem = list.closest('.js-product-category-li');
                const parentItemSiblings = getSiblings(parentItem);
                const currentBtn = parentItem.querySelector('.js-product-category-btn');
                const parent2nd = parentItem.parentNode;
                const item2nd = parent2nd.closest('.js-product-category-li');
                const isItem2nd = item2nd == null;
  
                // скрыть соседний список
                list.classList.remove('active');
  
                // показать соседнюю кнопку
                currentBtn.classList.remove('hide');
  
                // открыть список выше
                parentItem.classList.remove('hide');
                parentItemSiblings.forEach((item) => {
                  item.classList.remove('hide');
                });
  
                // передать текст в рут кнопку
                if(!isItem2nd){
                  const backBtnText = item2nd.querySelector('.js-product-category-btn').innerHTML;
                  btnRootTextNode.innerHTML = backBtnText;
                }else{
                  btnRootTextNode.innerHTML = btnRootText;
                }
  
              }
            }
          });
        };
  
        if(isActive){
          stepBack();
        }else{
          expand();
        }
      });
  
      btns.forEach((btn) => {
        btn.addEventListener('click', () => {
          const currentList = btn.closest('.js-product-category-list');
          const currentItem = btn.closest('.js-product-category-li');
          const siblings = getSiblings(currentItem);
          const nestedList = currentItem.querySelector('.js-product-category-list');
          const currentBtnText = btn.innerHTML;
  
          btn.classList.add('hide');
          btnRootTextNode.innerHTML = currentBtnText;
  
          siblings.forEach((item) => {
            item.classList.add('hide');
          });
  
          currentItem.classList.remove('hide');
  
          nestedList.classList.add('active');
        });
      });
    });
  };
  
  const filter = () => {
    const sections = document.querySelectorAll('.js-filter');
  
    sections.forEach((section) => {
      const btns = section.querySelectorAll('.js-filter-btn');
      const content = section.querySelector('.js-filter-content');
  
      btns.forEach((btn) => {
        btn.addEventListener('click', () => {
          const isActive = section.classList.contains('active');
  
          if(isActive){
            section.classList.remove('active');
          }else{
            section.classList.add('active');
          }
        });
      });
    });
  };
  
  const categorySlider = () => {
    const sliders = document.querySelectorAll('.js-category-slider');
  
    sliders.forEach((slider) => {
      let sliderObj = tns;
      const options = {
        container: '.js-category-slider',
        autoWidth: true,
        gutter: 14,
        mouseDrag: true,
        swipeAngle: false,
        controls: true,
        loop: false,
        nav: false,
        responsive: {
          0: {
            controls: false
          },
          1280: {
            controls: true
          }
        }
      };
  
      // slider init
      sliderObj = tns(options);
    });
  };
  
  const sort = () => {
    const sections = document.querySelectorAll('.js-sort');
  
    sections.forEach((section) => {
      const btn = section.querySelector('.js-sort-btn');
  
  
        document.documentElement.addEventListener('click', function (event) {
        	var isClickWithinOpenedDiv = top_walker(event.target, function (node) {
        		return node === section
        	});
        	if (isClickWithinOpenedDiv) {
  
        	}else{
            section.classList.remove('active');
          }
        }, true);
  
      btn.addEventListener('click', () => {
        const isActive = section.classList.contains('active');
  
        if(isActive){
          section.classList.remove('active');
        }else{
          section.classList.add('active');
        }
      });
    });
  };
  
  const searchData = () => {
  
  };
  
  const BG = {};
  BG.body = document.querySelector('body');
  BG.ESC_CODE = 27;
  BG.siteContent = document.querySelector('.site-content');
  BG.footer = document.querySelector('.page-footer');
  BG.isIe11 = !!window.MSInputMethodContext && !!document.documentMode;
  
  if(BG.isIe11){
    BG.body.classList.add('ie11');
  }
  
  function top_walker (node, test_func, last_parent) {
  	while ( node && node !== last_parent ) {
  		if ( test_func(node) ) {
  			return node;
  		}
  		node = node.parentNode;
  	}
  }
  
  const onPageRdy = () => {
    // utility
    select();
    field();
    counter();
  
    // specific
    cardCart();
    cartPopup();
    category();
    categorySelect();
    rating();
    headerMenu();
    productCategory();
    filter();
    sort();
  
    // sliders
    catalogSlider();
    categoryCarousel();
    categorySlider();
  };
  
  onPageRdy();
  
});
